<template><span>


<span v-for="a in arr" :class="nodecor ? undefined : $css.naoword">
	<span v-if="a[0] != '鰭'">{{a}}</span>
	<span v-else-if="a.slice(1) == '\n'" > <br> </span>
	<span v-else-if="no_popover" >{{ a.slice(1) }}</span>
	<span class="nw" v-else v-popover:naoword="{on_open: () => make_popover(a.slice(1))}" >{{ a.slice(1) }}</span>
</span>

<v-btn v-if="pronounce" @click="$nao.pronounce( kk(text) )" icon><v-icon> mdi-volume-high </v-icon></v-btn>

</span></template>

<script>
// v-popover:naoword="{on_open: () => make_popover(a.slice(1))}"
// <v-btn v-if="pronounce" @click="$nao.pronounce($nao.disassemble(text))" icon><v-icon> mdi-volume-high </v-icon></v-btn>

import Popover from '@/components/Popover'

export default {
	components: {Popover},
	props: {
		n: String,
		o: String,
		pronounce: Boolean,
		nodecor: Boolean,
		no_popover: Boolean
	},
	data() {
		return {
			
		}
	},
	computed: {
		text() {
			return  this.n ? this.$nao.unmake_neutral(this.n) : this.o;
		},
		arr() {
			let res = this.text.replace(/([\w']+|\n)/g, '🉇鰭$1🉇').split('🉇').filter(x => x.length > 0);
			//console.log(res);
			return res;
		}
	},
	created() {
		
	},
	methods: {
		make_popover(word) {
			let tr = this.$nao.translate_sentence(word).map( w => this.$nao.tr_from_Nao(w) || {nao: w, main_tr: null } );

			this.$popover_naoword(tr);
		},
		kk() {
			let res = [];
			for (let w of this.$nao.translate_sentence(this.text) ) {
				res = res.concat( this.$nao.disassemble(w) );
			}
			return res;
		}
	}
}
</script>

<style scoped>

.nw:hover {
	color: red;
	cursor: pointer;
}

</style>