<template><div>

	<v-card class="d-none d-md-flex contnav">
		<v-list dense>
	      <v-subheader> Lessons </v-subheader>
	      <v-list-item-group
	        color="primary"
	      >
	        <v-list-item
	          v-for="(lesson, i) in lesson_list"
	          :key="i"
	          router :to="'/textbook/' + lesson.route"
	        >
	          <v-list-item-content>
	            <v-list-item-title v-text="lesson.name"></v-list-item-title>
	          </v-list-item-content>
	        </v-list-item>
	      </v-list-item-group>
	    </v-list>
	</v-card>

	<div @click="drawer = !drawer" class="pull_drawer d-md-none elevation-6">
		<v-icon> mdi-arrow-expand-right </v-icon>
	</div> 

	<v-flex d-flex justify-center justify-md-end justify-lg-center>
		<v-flex xs12 sm10 md8 lg6 px-3 style="overflow: hidden">
			<component ref="textbook" :is="Textbook || $g.NotTranslated"></component>
		</v-flex>
	</v-flex>

	<v-navigation-drawer temporary v-model="drawer" app>
		<v-list dense>
	      <v-subheader> Lessons </v-subheader>
	      <v-list-item-group
	        color="primary"
	      >
	        <v-list-item
	          v-for="(lesson, i) in lesson_list"
	          :key="i"
	          router :to="'/textbook/' + lesson.route"
	        >
	          <v-list-item-content>
	            <v-list-item-title v-text="lesson.name"></v-list-item-title>
	          </v-list-item-content>
	        </v-list-item>
	      </v-list-item-group>
	    </v-list>
	</v-navigation-drawer>

</div></template>
		

<script>

import i18n from '@/i18n'
import Vue from 'vue'

export default {
	components: {},
	data: function() {
		return {
			i18n,
			lesson_list: [],
			drawer: false
		}
	},
	methods: {

	},
	mounted() {
		this.lesson_list = this.$refs.textbook.all_lessons;
	},
	computed: {
		Textbook() {
			return i18n.choose[this.$store.state.language].components.Textbook;
		},
		xlesson_list() {
			if (!this.$refs.textbook) return [];
			return this.$refs.textbook.all_lessons;
		}
	}
}
</script>

<style>

.contnav {
	position: fixed;
	height: 100%;
	width: 300px;
}

.pull_drawer {
	cursor: pointer;
	background-color: #b9b8b8;
	xbackground-color: white;
	xborder: 1px solid black;
	position: fixed;
	top: 50vh;
	z-index: 2;
}

</style>