import store from './store'
import Vue from 'vue'
import App from './App.vue'
import router from './router'

import vuetify from './plugins/vuetify';
import Nao from './nao'
import i18n from './i18n'
import NotTranslated from '@/components/NotTranslated'
import Rhaboo from './rhaboo.min.js'
import './registerServiceWorker'


let ls = Rhaboo.persistent("rhaboo");
if (!ls.search_history) ls.write('search_history', []);

Vue.prototype.$ls = ls;

let code = window.localStorage.getItem("language") || 'en';
store.commit('change_language', code);

//Vue.component('NotTranslated', NotTranslated);

Vue.prototype.$nao = Nao;

Vue.config.productionTip = false;

Vue.filter('t', i18n.$t);
Vue.filter('u', Nao.unmake_neutral);

Vue.prototype.$t = i18n.$t;

Vue.prototype.$css = {
	naoword: 'main_color font-weight-medium',
	title: 'text-h4 text-md-h3 text-center pa-5'
};

Vue.prototype.$g = {
	NotTranslated,
	cmn: [
		{icon: 'discord', url: 'https://discord.gg/nBB6NqS', color: "#7289da"},
		{icon: 'telegram', url: 'https://t.me/naolangbot', color: "#23aae8"},
		{icon: 'reddit', url: 'https://www.reddit.com/r/naolanguage/', color: "#FF4500"},
		{icon: 'facebook', url: 'https://www.facebook.com/naolanguage/', color: "#3b5998"},
	],
	updates: [
		{date: '2021-02-05', url: '/textbook/colors', status: 'new', comment: 'Colors page was brought from the old website and now looks good on mobile.', language: 'en'},
	]
};



new Vue({
	router,
	store,
	vuetify,
	render: h => h(App)
}).$mount('#app')




